import { Component, OnInit } from '@angular/core';
import { fadeInUp400ms } from '../../../../../@vex/animations/fade-in-up.animation';
import { stagger60ms } from '../../../../../@vex/animations/stagger.animation';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icPlus from '@iconify/icons-fa-solid/plus';
import icMinus from '@iconify/icons-fa-solid/minus';
import icList from '@iconify/icons-ic/baseline-list-alt';

import { ProductService } from '../../../../services/voicebot/bot/bot.service';
import { UtilService } from '../../../../services/utils/util/util.service';
import { Breadcrumb } from '../../../../interfaces/utils/Breadcrumb';
import {
  FormProduct,
  FieldTypeVoicebot,
} from '../../../../interfaces/voicebot/field-type-voicebot';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import { Observable } from 'rxjs/internal/Observable';

import icReupload from '@iconify/icons-ic/baseline-file-upload';
import { WhareService } from 'src/app/services/wharehouse/wharehouse.service';
import { BotResponse } from 'src/app/interfaces/voicebot/bot-response';

@Component({
  selector: 'vex-form-bot',
  templateUrl: './form-bot.component.html',
  styleUrls: ['./form-bot.component.scss'],
  animations: [stagger60ms, fadeInUp400ms],
})
export class FormBotComponent implements OnInit {
  //-------------------
  formGroup: FormGroup;
  submitted = false;
  isNew = true;
  visible = false;
  //-------------------

  icMoreVert = icMoreVert;
  icPlus = icPlus;
  icMinus = icMinus;
  icList = icList;
  icReupload = icReupload;

  //form: any;

  breadcrumbs: Breadcrumb[];

  fields: FieldTypeVoicebot[];
  field: FieldTypeVoicebot;

  loading = false;
  form: FormProduct;

  id: string;

  percentage: Observable<number>;

  urlFile: string;
  publicUrl: any = '';

  btnSubmit: 'Add' | 'Modify' = 'Add';

  constructor(
    private formBuilder: FormBuilder,
    private productService: ProductService,
    private util: UtilService,
    private route: ActivatedRoute,
    private router: Router,
    private whareHouse: WhareService
  ) {
    if (this.route.snapshot.paramMap.get('id') !== null) {
      this.id = this.route.snapshot.paramMap.get('id');

      this.breadcrumbs = [
        { url: '/voicebot/bots', text: 'Products' },
        {
          url: '/voicebot/bot/' + this.id,
          text: 'Edit Product',
        },
      ];
    } else {
      this.breadcrumbs = [
        { url: '/voicebot/bots', text: 'Products' },
        {
          url: '/voicebot/bots/',
          text: 'Add product',
        },
      ];
    }

    const clase = this;
    if (this.id !== undefined) {
      const product = this.router.getCurrentNavigation().extras
        .state as BotResponse;
      this.btnSubmit = 'Modify';
      this.isNew = false;
      clase.util.openMessage('Bot Cargado!', 'Completado', 'Primary');
      this.breadcrumbs[1].text = product.name;
      clase.formGroup = this.formBuilder.group({
        name: [product.name, [Validators.required]],
        amount: [product.amount, [Validators.required]],

        fields: this.formBuilder.array([]),
        coordinates: this.formBuilder.array([]),
      });
      //clase.addLoadField(data.fields);
      //clase.addLoadCoordinate(data.coordinates);
    } else {
      this.formGroup = this.formBuilder.group({
        name: ['', [Validators.required]],
        amount: ['', [Validators.required]],

        fields: this.formBuilder.array([]),
        coordinates: this.formBuilder.array([]),
      });
    }
  }

  ngOnInit() {}

  newExtraField() {
    return this.formBuilder.group({
      name: ['', [Validators.required, Validators.pattern('^[0-9a-z-]+$')]],
      type: ['', Validators.required],
      required: true,
    });
  }

  newExtraCoordinate() {
    return this.formBuilder.group({
      coordinate: ['', [Validators.required]],
      value: ['', Validators.required],
    });
  }

  newExtraFieldLoad(field: FieldTypeVoicebot) {
    return this.formBuilder.group({
      name: [
        field.name,
        [Validators.required, Validators.pattern('^[0-9a-z-]+$')],
      ],
      type: [field.type, Validators.required],
      required: field.required,
    });
  }

  newExtraCoordinateLoad(coordinate: string, value: string) {
    return this.formBuilder.group({
      coordinate: [coordinate, [Validators.required]],
      value: [value, Validators.required],
    });
  }

  addLoadField(fields: FieldTypeVoicebot[]) {
    const control = <FormArray>this.formGroup.controls['fields'];
    console.log(fields);
    fields.forEach((field) => {
      const newExtraField = this.newExtraFieldLoad(field);
      control.push(newExtraField);
    });
  }

  addLoadCoordinate(coordinates: any[]) {
    const control = <FormArray>this.formGroup.controls['coordinates'];
    coordinates = Object.entries(coordinates[0]);

    coordinates.forEach((coordinate) => {
      const newExtraField = this.newExtraCoordinateLoad(
        coordinate[0],
        coordinate[1]
      );
      control.push(newExtraField);
    });
  }

  addField(groupControl: string) {
    let newExtraField;
    const control = <FormArray>this.formGroup.controls[groupControl];
    if (groupControl === 'fields') {
      newExtraField = this.newExtraField();
    } else {
      newExtraField = this.newExtraCoordinate();
    }
    control.push(newExtraField);
  }

  deleteField(i: number, groupControl: string) {
    this.gerArrayForms(groupControl).removeAt(i);
  }

  gerArrayForms(control: string) {
    return this.formGroup.get(control) as FormArray;
  }

  redirectListBots(): void {
    const url = `/voicebot/bots/`;
    this.router.navigate([url]);
  }

  submitHandler() {
    this.submitted = true;
    const clase = this;

    this.form = this.formGroup.value;
    // let object = {};
    // this.form.coordinates.forEach((element) => {
    //   object[element.coordinate] = element.value;
    // });

    this.form.wharehouse = this.whareHouse.selectedWharehouse$.getValue().id;
    if (!this.formGroup.invalid) {
      if (this.isNew === true) {
        this.productService.addBot(this.form).subscribe((response) => {
          clase.util.openMessage('ADDED!', 'Complete', 'Primary');
          clase.redirectListBots();
          clase.loading = false;
        });
      } else {
        this.productService
          .updateBot(this.id, this.form)
          .subscribe((response) => {
            clase.util.openMessage('UPDATED!', 'Complete', 'Primary');
            clase.redirectListBots();
            clase.loading = false;
          });
      }
    } else {
      clase.util.openMessage('Favor Validar Campos', 'Error');
    }
  }

  get f() {
    return this.formGroup.controls;
  }
}
